@import 'base/variables';

body {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto','Helvetica','Arial',sans-serif;
  justify-content: space-between;
  margin: 0;
  min-height: 100vh;
}

header {
  background-color: #F44336;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),
              0 3px 1px -2px rgba(0,0,0,.2),
              0 1px 5px 0 rgba(0,0,0,.12);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  padding: 10px;
  width: 100%;
}

header h1 {
  background: $bg;
  color: $red;
}

.card {
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),
              0 3px 1px -2px rgba(0,0,0,.2),
              0 1px 5px 0 rgba(0,0,0,.12);
  margin: 20px;
  max-width: 600px;
  padding: 10px;
}

.welcome {
  align-items: center;
  background-color: #E91E63;
  display: flex;
  height: 50px;
  justify-content: center;
}

p {
  padding: 10px;
  text-align: center;
}

.img-container {
  background-color: black;
}

img {
  display: block;
  margin: 0 auto;
  max-width: 100%;
}

a {
  color: #000;
}

footer {
  align-items: center;
  background-color: #F44336;
  box-sizing: border-box;
  display: flex;
  height: 7vh;
  justify-content: center;
  min-height: 50px;
  padding: 10px;
  width: 100%;
}
